import { model, Schema, models } from 'mongoose';

export default function getmodelNameModel() {
  const collectionName = 'modelName';
  if (models[collectionName]) {
    return model(collectionName);
  }
  const modelNameSchema = new Schema({
    consignmentType: {
      type: String,
      required: true,
    },
  });

  return model(collectionName, modelNameSchema);
}
